"use client"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Copy, Check, Share2 } from "lucide-react"
import { useState, useEffect } from "react"
import { useLanguage } from "@/contexts/language-context"

export function ReferralLink() {
  const [copied, setCopied] = useState(false)
  const [referralCode, setReferralCode] = useState("")
  const [loading, setLoading] = useState(true)
  const { language } = useLanguage()

  useEffect(() => {
    const loadReferralCode = async () => {
      try {
        const res = await fetch("/api/referrals", { cache: "no-store" })
        if (res.ok) {
          const data = await res.json()
          setReferralCode(data.referralCode || "")
        }
      } catch (error) {
        console.error("Failed to load referral code:", error)
      } finally {
        setLoading(false)
      }
    }
    loadReferralCode()
  }, [])

  const referralLink = referralCode 
    ? `${typeof window !== 'undefined' ? window.location.origin : ''}/register?ref=${referralCode}`
    : ""

  const copyToClipboard = () => {
    if (!referralLink) return
    navigator.clipboard.writeText(referralLink)
    setCopied(true)
    setTimeout(() => setCopied(false), 2000)
  }

  return (
    <div className="card-gradient rounded-xl p-6 lg:p-8 border border-border/50">
      <div className="flex items-start gap-4 mb-6">
        <div className="w-12 h-12 rounded-lg bg-primary/20 flex items-center justify-center">
          <Share2 className="w-6 h-6 text-primary" />
        </div>
        <div>
          <h2 className="text-xl font-bold text-foreground mb-1">
            {language === "ru" ? "–í–∞—à–∞ —Ä–µ—Ñ–µ—Ä–∞–ª—å–Ω–∞—è —Å—Å—ã–ª–∫–∞" : "Your Referral Link"}
          </h2>
          <p className="text-sm text-muted-foreground">
            {language === "ru" 
              ? "–ü–æ–¥–µ–ª–∏—Ç–µ—Å—å —ç—Ç–æ–π —Å—Å—ã–ª–∫–æ–π —Å –¥—Ä—É–∑—å—è–º–∏ –∏ –ø–æ–ª—É—á–∞–π—Ç–µ 10% –æ—Ç –∏—Ö –ø–æ–ø–æ–ª–Ω–µ–Ω–∏–π"
              : "Share this link with friends and earn 10% from their top-ups"
            }
          </p>
        </div>
      </div>

      <div className="flex gap-3">
        <Input 
          value={loading ? (language === "ru" ? "–ó–∞–≥—Ä—É–∑–∫–∞..." : "Loading...") : referralLink} 
          readOnly 
          className="bg-background/50 border-border/50 font-mono text-sm" 
          disabled={loading}
        />
        <Button 
          onClick={copyToClipboard} 
          className="bg-primary hover:bg-primary/90 gap-2 min-w-[120px]"
          disabled={loading || !referralLink}
        >
          {copied ? (
            <>
              <Check className="w-4 h-4" />
              {language === "ru" ? "–°–∫–æ–ø–∏—Ä–æ–≤–∞–Ω–æ" : "Copied"}
            </>
          ) : (
            <>
              <Copy className="w-4 h-4" />
              {language === "ru" ? "–ö–æ–ø–∏—Ä–æ–≤–∞—Ç—å" : "Copy"}
            </>
          )}
        </Button>
      </div>

      <div className="mt-6 p-4 rounded-lg bg-primary/10 border border-primary/30">
        <p className="text-sm text-foreground">
          <span className="font-semibold">
            {language === "ru" ? "–ö–∞–∫ —ç—Ç–æ —Ä–∞–±–æ—Ç–∞–µ—Ç:" : "How it works:"}
          </span>{" "}
          {language === "ru"
            ? "–ö–æ–≥–¥–∞ –∫—Ç–æ-—Ç–æ —Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä—É–µ—Ç—Å—è –ø–æ –≤–∞—à–µ–π —Å—Å—ã–ª–∫–µ –∏ –ø–æ–ø–æ–ª–Ω—è–µ—Ç –±–∞–ª–∞–Ω—Å, –≤—ã –ø–æ–ª—É—á–∞–µ—Ç–µ 10% –æ—Ç —Å—É–º–º—ã –ø–æ–ø–æ–ª–Ω–µ–Ω–∏—è –Ω–∞ —Å–≤–æ–π —Å—á–µ—Ç. –í—ã–ø–ª–∞—Ç—ã –ø—Ä–æ–∏—Å—Ö–æ–¥—è—Ç –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏."
            : "When someone registers using your link and tops up their balance, you get 10% of the top-up amount credited to your account. Payouts are automatic."
          }
        </p>
      </div>
    </div>
  )
}
